
.fotoGlowne img{
    width: 100vw;
    margin-bottom: 50px;
    height: 700px;

}


.container h2{
    position: absolute;
    color: white;
    font-size: 30px;
    left: 0;
    right: 0;
    text-align: center;
    top: 30%;
    font-family: 'Cinzel', serif;
    text-shadow: 3px 3px 3px #000;
}

.container p{
    position: absolute;
    color: white;
    font-size: 50px;
    left: 0;
    right: 0;
    text-align: center;
    top: 40%;
    font-family: 'Cinzel', serif;
    text-shadow: 3px 3px 3px #000;

}
.galerea{
    overflow: hidden;
    width: 100vw;

    padding: 10px 5% 50px 10%;

}


.galerea .galereaItem{
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;

}


.galerea a:hover{
    cursor: zoom-in;


}
.galerea a:hover img{

    opacity: 0.8;
}

.link{
    position: relative;
    display: inline-block;
    width: 300px; /* ширина блока */
    margin: 5px;
}

.galereaItem img{
    width: 300px;
    height: 300px;
    object-fit: cover!important;
    border: solid 1px #999;
    padding:5px;
    opacity: 1;
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;



}

.galereaItem:hover img{
    opacity: .5
}

.galereaItem h3{
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    opacity: 0;
    color: #fff; /* цвет иконки */
    font-size: 30px; /* размер иконки */

}
.galereaItem:hover h3{
    opacity: 1;
    z-index: 1000;
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
}
.lg-thumb-item {

    filter: grayscale(100%);
    will-change: filter;
    transition: filter 0.12s ease-in, border-color 0.12s ease;}
.lg-thumb-item:hover{
    filter: grayscale(0);
    border-color: #545454;

}


